import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';
import Ripple from 'components/ripple/Ripple';
import Button from 'components/button/Button';
import CloseSVG from 'assets/images/close.svg';
import LogoSVG from 'assets/images/logo.svg';
import LogoSVG2 from 'assets/images/logo-purple.svg';
import LogoSVG3 from 'assets/images/logo-blue.svg';
import styles from './Menu.module.scss';

function MenuItem({ title }) {
  const { settings } = useSelector((state) => {
    return state;
  });

  return (
    <Ripple>
      <div
        className={`${styles.menuItem} ${
          styles[
            `mobileMenuItemBordered${settings.data.mobileMenuItemBordered}`
          ]
        } ${
          styles[`mobileMenuItemSpacing${settings.data.mobileMenuItemSpacing}`]
        }`}
      >
        {title}
      </div>
    </Ripple>
  );
}
function Menu() {
  const { settings } = useSelector((state) => {
    return state;
  });

  useEffect(() => {
    let menu = document.querySelector('.mobile-menu');
    let closeIcon = document.querySelector('.close-icon');

    menu.addEventListener('transitionend', onTransitionEnd, false);
    menu.addEventListener('click', handleClick, false);
    closeIcon.addEventListener('click', toggleClassMenu, false);

    function handleClick(event) {
      if (event.target === menu) {
        toggleClassMenu();
      }
    }
    function toggleClassMenu() {
      menu.classList.add('menu--animatable');
      if (!menu.classList.contains('menu--visible')) {
        menu.classList.add('menu--visible');
        document.body.style.overflow = 'hidden';
      } else {
        menu.classList.remove('menu--visible');
        document.body.style.overflow = 'initial';
      }
    }
    function onTransitionEnd() {
      menu.classList.remove('menu--animatable');
    }

    return () => {
      menu.removeEventListener('transitionend', onTransitionEnd, false);
      menu.removeEventListener('click', handleClick, false);
      closeIcon.removeEventListener('click', toggleClassMenu, false);
    };
  }, []);

  return (
    <div
      className={`mobile-menu ${styles.container} ${
        styles[`headerFixed${settings.data.headerFixed}`]
      }`}
    >
      <div
        className={`app-menu mobileSidebar${
          settings.data.mobileSidebar
        } ${`backgroundColor${settings.data.backgroundColor}`}`}
      >
        <div className={styles.header}>
          <div className={styles.appInfo}>
            <img
              className={`${styles.logo} ${
                styles[`appLogoSize${settings.data.appLogoSize}`]
              }`}
              src={
                settings.data.primaryColor === 1
                  ? LogoSVG
                  : settings.data.primaryColor === 2
                  ? LogoSVG2
                  : settings.data.primaryColor === 3
                  ? LogoSVG3
                  : LogoSVG
              }
              alt="ثمین‌کوین"
            />
            <h1
              className={`${
                styles[`appNameColor${settings.data.appNameColor}`]
              } ${styles[`appNameSize${settings.data.appNameSize}`]}  ${
                styles[`primaryColor${settings.data.primaryColor}`]
              }`}
            >
              ثمین‌کوین
            </h1>
          </div>
          <img className="close-icon" src={CloseSVG} alt="بستن منو" />
        </div>
        <div className={styles.content}>
          <div className={styles.menu}>
            <MenuItem title="روند بازار" />
            <MenuItem title="خرید و فروش آسان" />
            <MenuItem title="اخبار" />
            <MenuItem title="اطلاعیه" />
            <MenuItem title="کوین‌ها" />
            <MenuItem title="تبلیغات" />
            <MenuItem title="جوایز" />
            <MenuItem title="کارت هدیه" />
            <MenuItem title="معرفی به دوستان" />
            <MenuItem title="NFT" />
            <MenuItem title="درباره" />
            <MenuItem title="تماس با ما" />
          </div>
        </div>
        <div className={styles.footer}>
          <Button className={styles.button} title="ورود" />
          <Button className={styles.button} title="ثبت نام" color="primary" />
        </div>
      </div>
    </div>
  );
}

export default Menu;
