import { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import Lottie from 'react-lottie';
import Ripple from 'components/ripple/Ripple';
import Button from 'components/button/Button';
import Picker from 'components/picker/Picker';
import * as animationData from 'assets/images/lottie/102720-cryptocurrency-bitcoin-working-loop.json';
import * as animationData2 from 'assets/images/lottie/63309-crypto-exchange.json';
import * as securityAnimationData1 from 'assets/images/lottie/88252-data-security.json';
import * as securityAnimationData2 from 'assets/images/lottie/90342-security.json';
import * as securityAnimationData3 from 'assets/images/lottie/73812-cloud-computing-security.json';
import * as securityAnimationData4 from 'assets/images/lottie/78078-security-system.json';
import * as feeAnimationData1 from 'assets/images/lottie/93561-customer-service.json';
import * as feeAnimationData2 from 'assets/images/lottie/84623-transaction-history.json';
import * as globalizationData1 from 'assets/images/lottie/71804-business.json';
import * as globalizationData2 from 'assets/images/lottie/72333-business-partners-handshaking-collaboration.json';
import * as globalizationData3 from 'assets/images/lottie/73195-business-partnership-contract-handshake.json';
import BTCSVG from 'assets/images/coins/btc.svg';
import ETHSVG from 'assets/images/coins/eth.svg';
import BCHSVG from 'assets/images/coins/bch.svg';
import XRPSVG from 'assets/images/coins/xrp.svg';
import LINKSVG from 'assets/images/coins/link.svg';
import USDTSVG from 'assets/images/coins/usdt.svg';
import IRRSVG from 'assets/images/coins/rial.png';
import BTCChartSVG from 'assets/images/temp/btc-chart.png';
import ETHChartSVG from 'assets/images/temp/eth-chart.png';
import BCHChartSVG from 'assets/images/temp/bch-chart.png';
import XRPChartSVG from 'assets/images/temp/xrp-chart.png';
import LINKChartSVG from 'assets/images/temp/link-chart.png';
import Promotion1PNG from 'assets/images/promotions/1.png';
import Promotion2PNG from 'assets/images/promotions/2.png';
import Promotion3PNG from 'assets/images/promotions/3.png';
import Promotion4PNG from 'assets/images/promotions/4.png';
import Promotion5PNG from 'assets/images/promotions/5.png';
import UpDownSVG from 'assets/images/up-down.svg';
import InstagramSVG from 'assets/images/socials/instagram.svg';
import LinkedinSVG from 'assets/images/socials/linkedin.svg';
import styles from './HomePage.module.scss';

const TETHER_PRICES = {
  sell: 32228,
  buy: 32608,
};
const COIN_PRICES = [
  {
    title: 'ریال',
    symbol: 'IRR',
    sellPrice: TETHER_PRICES.sell,
    butPrice: TETHER_PRICES.buy,
    image: IRRSVG,
  },
  {
    title: 'تتر',
    symbol: 'USDT',
    sellPrice: 1,
    butPrice: 1,
    image: USDTSVG,
  },
  {
    title: 'بیت‌کوین',
    symbol: 'BTC',
    sellPrice: 30080 * TETHER_PRICES,
    butPrice: 30120 * TETHER_PRICES,
    image: BTCSVG,
  },
];
const COINS = [
  {
    title: 'بیت‌کوین',
    enTitle: 'Bitcoin',
    symbol: 'BTC',
    logo: BTCSVG,
    chart: BTCChartSVG,
    price: 962188534,
    priceUsd: 30093,
    changes: -0.69,
  },
  {
    title: 'اتریوم',
    enTitle: 'Ethereum',
    symbol: 'ETH',
    logo: ETHSVG,
    chart: ETHChartSVG,
    price: 57007117,
    priceUsd: 1789.88,
    changes: -1.07,
  },
  {
    title: 'بیت‌کوین کش',
    enTitle: 'Bitcoin Cash',
    symbol: 'BCH',
    logo: BCHSVG,
    chart: BCHChartSVG,
    price: 5565226,
    priceUsd: 174.89,
    changes: -0.64,
  },
  {
    title: 'ریپل',
    enTitle: 'Ripple',
    symbol: 'XRP',
    logo: XRPSVG,
    chart: XRPChartSVG,
    price: 12913,
    priceUsd: 0.4,
    changes: 0.57,
  },
  {
    title: 'چین‌لینک',
    enTitle: 'Chainlink',
    symbol: 'LINK',
    logo: LINKSVG,
    chart: LINKChartSVG,
    price: 283417,
    priceUsd: 8.64,
    changes: -5.01,
  },
];

function HomePage() {
  const [coinTab, setCoinTab] = useState(1);
  const [from, setFrom] = useState(COIN_PRICES[1]);
  const [fromAmount, setFromAmount] = useState(COIN_PRICES[1].sellPrice);
  const [to, setTo] = useState(COIN_PRICES[0]);
  const [toAmount, setToAmount] = useState(COIN_PRICES[0].sellPrice);
  const { settings } = useSelector((state) => {
    return state;
  });

  useEffect(() => {
    const slider = document.querySelector(`.${styles.slider}`);
    let isDown = false;
    let startX;
    let scrollLeft;

    slider.addEventListener('mousedown', (e) => {
      isDown = true;
      slider.classList.add('active');
      slider.style.cursor = 'grabbing';
      startX = e.pageX - slider.offsetLeft;
      scrollLeft = slider.scrollLeft;
    });
    slider.addEventListener('mouseleave', () => {
      isDown = false;
      slider.classList.remove('active');
      slider.style.cursor = 'grab';
    });
    slider.addEventListener('mouseup', () => {
      isDown = false;
      slider.classList.remove('active');
      slider.style.cursor = 'grab';
    });
    slider.addEventListener('mousemove', (e) => {
      if (!isDown) return;
      e.preventDefault();
      const x = e.pageX - slider.offsetLeft;
      const walk = (x - startX) * 1; //scroll-fast
      slider.scrollLeft = scrollLeft - walk;
      //   console.log(walk);
    });
  }, []);

  let securityAnimationData;
  switch (settings.data.securityAnimation) {
    case 1:
      securityAnimationData = securityAnimationData1;
      break;

    case 2:
      securityAnimationData = securityAnimationData2;
      break;

    case 3:
      securityAnimationData = securityAnimationData3;
      break;

    case 4:
      securityAnimationData = securityAnimationData4;
      break;

    default:
      securityAnimationData = securityAnimationData1;
      break;
  }

  let feeAnimationData;
  switch (settings.data.feeAnimation) {
    case 1:
      feeAnimationData = feeAnimationData1;
      break;

    case 2:
      feeAnimationData = feeAnimationData2;
      break;

    default:
      feeAnimationData = feeAnimationData1;
      break;
  }

  let globalizationData;
  switch (settings.data.globalizationAnimation) {
    case 1:
      globalizationData = globalizationData1;
      break;

    case 2:
      globalizationData = globalizationData2;
      break;

    case 3:
      globalizationData = globalizationData3;
      break;

    default:
      globalizationData = globalizationData1;
      break;
  }

  return (
    <div
      className={`${styles.container} ${
        styles[`backgroundColor${settings.data.backgroundColor}`]
      } ${styles[`headerFixed${settings.data.headerFixed}`]}`}
    >
      <div
        className={`${styles.heading} ${
          styles[`mobileFirstViewImage${settings.data.mobileFirstViewImage}`]
        }`}
      >
        <div className={styles.title}>
          <div
            className={`${styles.header} ${
              styles[`firstViewFontSize${settings.data.firstViewFontSize}`]
            }`}
          >
            {settings.data.headerSubHeaderSwap === 1 ? (
              <>
                <h1>
                  پلتفرم خرید و فروش امن
                  <br />و سریع ارزهای دیجیتال
                </h1>
                <h2>گامی امن به سوی آینده دیجیتال</h2>
              </>
            ) : null}
            {settings.data.headerSubHeaderSwap === 2 ? (
              <>
                <h1>گامی امن به سوی آینده دیجیتال</h1>
                <h2>پلتفرم خرید و فروش امن و سریع ارزهای دیجیتال</h2>
              </>
            ) : null}
            {settings.data.startButton === 1 ? (
              <Button
                className={styles.button}
                title="همین حالا شروع کنید"
                color="primary"
              />
            ) : null}
            {settings.data.startButton === 2 ? (
              <div className={styles.inputGroup}>
                <input
                  className={
                    styles[`buttonRadius${settings.data.buttonRadius}`]
                  }
                  type="text"
                  placeholder="شماره همراه یا ایمیل"
                />
                <Button
                  className={`${styles.inputGroupButton} ${
                    styles[`buttonRadius${settings.data.buttonRadius}`]
                  }`}
                  title="شروع کنید"
                  color="primary"
                />
              </div>
            ) : null}
            {[1, 2].indexOf(settings.data.showOtcScrollButton) !== -1 ? (
              <>
                {settings.data.showOtcScrollButton === 1 ? (
                  <div
                    className={styles.jumpToSection}
                    onClick={() => {
                      document
                        .querySelector(`.${styles.exchangeContainer}`)
                        .scrollIntoView({ behavior: 'smooth' });
                    }}
                  >
                    خرید و فروش آسان ↓
                  </div>
                ) : null}
                {settings.data.showOtcScrollButton === 2 ? (
                  <Button
                    className={styles.jumpToSectionButton}
                    title="خرید و فروش آسان ↓"
                    color="primary"
                    onClick={() => {
                      document
                        .querySelector(`.${styles.exchangeContainer}`)
                        .scrollIntoView({ behavior: 'smooth' });
                    }}
                  />
                ) : null}
              </>
            ) : null}
          </div>
        </div>
        <div className={styles.content}>
          <Lottie
            options={{
              loop: true,
              autoplay: true,
              animationData: animationData,
              rendererSettings: {
                preserveAspectRatio: 'xMidYMid slice',
              },
            }}
          />
        </div>
      </div>

      <div className={styles.marketsContainer}>
        <div className={styles.header}>
          <div className={styles.tabs}>
            <Ripple
              onClick={() => {
                setCoinTab(1);
              }}
            >
              <div
                className={`${styles.tab} ${
                  coinTab === 1 ? styles.active : ''
                } ${styles[`primaryColor${settings.data.primaryColor}`]}`}
              >
                محبوب‌ترین‌ها
              </div>
            </Ripple>
            <Ripple
              onClick={() => {
                setCoinTab(2);
              }}
            >
              <div
                className={`${styles.tab} ${
                  coinTab === 2 ? styles.active : ''
                }`}
              >
                کوین‌های جدید
              </div>
            </Ripple>
          </div>
        </div>
        <div className={styles.tableContainer}>
          <table>
            <thead>
              <tr>
                <th>کوین</th>
                <th>آخرین قیمت</th>
                <th>تغییرات</th>
                <th>نمودار تغییرات</th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {COINS.map((coin, index) => {
                return (
                  <tr key={index}>
                    <td>
                      <div className={styles.cellContainer}>
                        <img
                          className={styles.coinLogo}
                          src={coin.logo}
                          alt={coin.title}
                        />
                        <div className={styles.description}>
                          <div className={styles.title}>{coin.title}</div>
                          <abbr
                            className={styles.subTitle}
                            title={coin.enTitle}
                          >
                            {coin.symbol}
                          </abbr>
                        </div>
                      </div>
                    </td>
                    <td>
                      <div className={styles.cellContainer}>
                        <div className={styles.description}>
                          <div className={styles.title}>
                            {coin.price.toLocaleString()}{' '}
                            <span
                              className={`${styles.currencyName} ${
                                styles[
                                  `currencyFontWeight${settings.data.currencyFontWeight}`
                                ]
                              }`}
                            >
                              تومان
                            </span>
                          </div>
                          <div className={styles.subTitle}>
                            ${coin.priceUsd.toLocaleString()}
                          </div>
                        </div>
                      </div>
                    </td>
                    <td>
                      <div className={styles.cellContainer}>
                        <div
                          className={`${styles.priceChanges} ${
                            coin.changes >= 0 ? styles.positive : ''
                          } ${
                            styles[
                              `coinPriceChangesColor${settings.data.coinPriceChangesColor}`
                            ]
                          }`}
                        >
                          {coin.changes}%
                        </div>
                      </div>
                    </td>
                    <td>
                      <div className={styles.cellContainer}>
                        <img
                          className={styles.priceChart}
                          src={coin.chart}
                          alt={`نمودار قیمت ${coin.title}`}
                        />
                      </div>
                    </td>
                    <td>
                      <div className={styles.cellContainer}>
                        <Button title="خرید و فروش" color="primary" />
                      </div>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
        <div className={styles.footer}>
          <div className={styles.more}>موارد بیشتر ←</div>
        </div>
      </div>

      <div className={styles.promotionContainer}>
        <div className={styles.slider}>
          <div className={styles.promotion}>
            <img src={Promotion1PNG} alt="تبلیغات" />
          </div>
          <div className={styles.promotion}>
            <img src={Promotion2PNG} alt="تبلیغات" />
          </div>
          <div className={styles.promotion}>
            <img src={Promotion3PNG} alt="تبلیغات" />
          </div>
          <div className={styles.promotion}>
            <img src={Promotion4PNG} alt="تبلیغات" />
          </div>
          <div className={styles.promotion}>
            <img src={Promotion5PNG} alt="تبلیغات" />
          </div>
          <div className={styles.promotion}>
            <img src={Promotion1PNG} alt="تبلیغات" />
          </div>
          <div className={styles.promotion}>
            <img src={Promotion2PNG} alt="تبلیغات" />
          </div>
        </div>
      </div>

      <div className={styles.exchangeContainer}>
        <div className={styles.description}>
          <div className={styles.texts}>
            <h2>خرید و فروش آسان</h2>
            <h3>
              به سادگی اقدام به خرید، فروش
              <br />و تبدیل ارزهای دیجیتال مورد علاقه خود کنید.
            </h3>
            <div className={styles.imageContainer}>
              <Lottie
                options={{
                  loop: true,
                  autoplay: true,
                  animationData: animationData2,
                  rendererSettings: {
                    preserveAspectRatio: 'xMidYMid slice',
                  },
                }}
              />
            </div>
          </div>
        </div>
        <div className={styles.content}>
          <div className={styles.calculatorContainer}>
            <form>
              <Picker
                containerClassName={styles.picker}
                options={COIN_PRICES}
                uniqueId="symbol"
                imageKey="image"
                value={from}
                onChange={(selected) => {
                  setFrom(selected);
                }}
                displayProcess={(option) => {
                  return `${option.title} (${option.symbol})`;
                }}
              />
              <div className={styles.inputContainer}>
                <input
                  aria-labelledby="paying-input-label"
                  type="text"
                  value={fromAmount}
                  onChange={(event) => {
                    setFromAmount(event.target.value);
                  }}
                />
                <label id="paying-input-label">مقدار پرداختی</label>
              </div>
              <img className={styles.reverse} src={UpDownSVG} alt="تبدیل" />
              <Picker
                containerClassName={styles.picker}
                options={COIN_PRICES}
                uniqueId="symbol"
                imageKey="image"
                value={to}
                onChange={(selected) => {
                  setTo(selected);
                }}
                displayProcess={(option) => {
                  return `${option.title} (${option.symbol})`;
                }}
              />
              <div className={styles.inputContainer}>
                <input
                  aria-labelledby="receiving-input-label"
                  type="text"
                  value={toAmount}
                  onChange={(event) => {
                    setToAmount(event.target.value);
                  }}
                />
                <label id="receiving-input-label">مقدار دریافتی</label>
              </div>
              <Button
                className={styles.button}
                title="خرید می‌کنم"
                color="primary"
              />
            </form>
          </div>
        </div>
      </div>

      <div className={styles.informationContainer}>
        <div className={styles.context}>
          <h2>
            بالاترین امنیت در خرید
            <br />و فروش رمزارز
          </h2>
          <h3>
            قوی‌ترین تیم توسعه‌دهنده و امنیت سایبری
            <br />و دارایی‌های دیجیتال
          </h3>
          <p></p>
        </div>
        <div className={styles.image}>
          <div className={styles.imageContainer}>
            <Lottie
              options={{
                loop: true,
                autoplay: true,
                animationData: securityAnimationData,
                rendererSettings: {
                  preserveAspectRatio: 'xMidYMid slice',
                },
              }}
            />
          </div>
        </div>
      </div>
      <div className={styles.informationContainer}>
        <div className={styles.context}>
          <h2>کمترین کارمزد در ایران</h2>
          <h3>
            کارمزد معاملات در مجموعه ثمین‌کوین
            <br />
            از تمامی صرافی‌ها پایین‌تر است.
          </h3>
          <p></p>
        </div>
        <div className={styles.image}>
          <div className={styles.imageContainer}>
            <Lottie
              options={{
                loop: true,
                autoplay: true,
                animationData: feeAnimationData,
                rendererSettings: {
                  preserveAspectRatio: 'xMidYMid slice',
                },
              }}
            />
          </div>
        </div>
      </div>
      <div className={styles.informationContainer}>
        <div className={styles.context}>
          <h2>استراتژی پیشرفت جهانی</h2>
          <h3>
            تیم مدیریت و برنامه‌ریزی استراتژیک مجموعه
            <br />
            ثمین‌کوین جمعی از نخبگان این حوزه هستند
          </h3>
          <p></p>
        </div>
        <div className={styles.image}>
          <div className={styles.imageContainer}>
            <Lottie
              options={{
                loop: true,
                autoplay: true,
                animationData: globalizationData,
                rendererSettings: {
                  preserveAspectRatio: 'xMidYMid slice',
                },
              }}
            />
          </div>
        </div>
      </div>

      {/* <div className={styles.unfinished}>
        در حال پیاده سازی...
        <br />
        محتوای معرفی مجموعه، اپلیکیشن‌ها و ...
      </div> */}

      <footer>
        <div className={styles.content}>
          <div className={`${styles.section} ${styles.wide}`}>
            <h3>ثمین‌کوین</h3>
            <p>
              ثمین‌کوین از بدو فعالیت بر این بوده که جمعی از برترین متخصصان،
              نخبگان و فعالان حوزه فین‌تک، برنامه‌نویسی، بازارهای مالی، دیجیتال
              مارکتینگ و برندینگ، اطلاع‌رسانی و پردازش‌گری بازارهای مالی را در
              یک خانواده‌ی صمیمی، متشکل، منسجم و کارآمد گرد هم آورد و اینک با
              افتتاح مجموعه‌ی آنلاین OTC قصد در گسترش این شبکه و قرار گرفتن در
              جایگاه اول این حوزه در مجامع بین‌المللی را دارد.
            </p>
          </div>
          <div className={styles.section}>
            <h3>دسترسی سریع</h3>
            <ul>
              <li>
                <a href="#">روند بازار</a>
              </li>
              <li>
                <a href="#">خرید و فروش آسان</a>
              </li>
              <li>
                <a href="#">درباره</a>
              </li>
              <li>
                <a href="#">تماس با ما</a>
              </li>
            </ul>
          </div>
          <div className={styles.section}>
            <h3>مطالب</h3>
            <ul>
              <li>
                <a href="#">اخبار</a>
              </li>
              <li>
                <a href="#">اطلاعیه</a>
              </li>
              <li>
                <a href="#">کوین‌ها</a>
              </li>
              <li>
                <a href="#">تبلیغات</a>
              </li>
            </ul>
          </div>
          <div className={styles.section}>
            <h3>ویژگی‌ها</h3>
            <ul>
              <li>
                <a href="#">جوایز</a>
              </li>
              <li>
                <a href="#">کارت هدیه</a>
              </li>
              <li>
                <a href="#">معرفی به دوستان</a>
              </li>
              <li>
                <a href="#">NFT</a>
              </li>
            </ul>
          </div>
          <div className={`${styles.section} ${styles.wide}`}>
            <h3>اطلاعات تماس</h3>
            <p>
              دفتر فنی و پشتیبانی: کرج، عظیمیه، حدفاصل میدان طالقانی و چهارراه
              پامچال، برج پارادایس، طبقه 5، واحد 501 - 504
            </p>
            <p>
              شماره تلفن: <a href="tel:+982634004400">34004400-026</a>
            </p>
            <p>
              خطوط پشتیبانی:{' '}
              <a href="tel:+982634003401">9-34003401-026 (9 خط)</a>
            </p>
            <p>
              شماره پشتیبانی واتساپ:{' '}
              <a
                href="https://wa.me/989924028690"
                target="_blank"
                rel="noreferrer"
              >
                09924028690
              </a>
            </p>
          </div>
        </div>
        <div className={styles.footNote}>
          <div className={styles.policy}>
            تمامی حقوق این وبسایت متعلق به ثمین‌کوین است.
          </div>
          <div className={styles.socialMedias}>
            <a
              href="https://linkedin.com/company/samincoin"
              target="_blank"
              rel="noreferrer"
            >
              <img src={LinkedinSVG} alt="لینکدین" />
            </a>
            <a
              href="https://instagram.com/samincoin"
              target="_blank"
              rel="noreferrer"
            >
              <img src={InstagramSVG} alt="اینستاگرام" />
            </a>
          </div>
        </div>
      </footer>
    </div>
  );
}

export default HomePage;
