import { useDispatch, useSelector } from 'react-redux';
import { SketchPicker } from 'react-color';
import CloseSVG from 'assets/images/close.svg';
import styles from './Settings.module.scss';

function Option({ title, type, value }) {
  const dispatch = useDispatch();
  const { settings } = useSelector((state) => {
    return state;
  });

  function isChecked() {
    return settings.data[type] === value;
  }
  function handleClick() {
    return dispatch({ type: 'settings/data', payload: { key: type, value } });
  }

  return (
    <label className={styles.option}>
      <input type="checkbox" checked={isChecked()} onChange={handleClick} />
      {title}
    </label>
  );
}
function Settings() {
  const dispatch = useDispatch();
  const { settings } = useSelector((state) => {
    return state;
  });

  function handleCloseClick() {
    dispatch({ type: 'settings/hide' });
  }
  function downloadObjectAsJson(exportObj, exportName) {
    const dataStr =
      'data:text/json;charset=utf-8,' +
      encodeURIComponent(JSON.stringify(exportObj));
    const downloadAnchorNode = document.createElement('a');

    exportName += `-${new Date()
      .toLocaleDateString()
      .replaceAll('/', '_')}-${new Date()
      .toLocaleTimeString()
      .substring(0, 8)
      .replaceAll(':', '_')}`;
    downloadAnchorNode.setAttribute('href', dataStr);
    downloadAnchorNode.setAttribute('download', exportName + '.json');
    document.body.appendChild(downloadAnchorNode); // required for firefox
    downloadAnchorNode.click();
    downloadAnchorNode.remove();
  }
  function handleDownload() {
    downloadObjectAsJson(settings.data, 'website_configuration');
  }

  return (
    <div className={styles.container}>
      <div className={styles.header} onClick={handleCloseClick}>
        <img src={CloseSVG} alt="بستن" />
        بستن
        <span className={styles.notice}>
          (برای باز کردن مجدد تنظیمات روی لوگوی ثمین‌کوین کلیک کنید)
        </span>
      </div>
      <hr />
      <div className={styles.content}>
        <div className={styles.section}>
          <div className={styles.title}>رنگ پشت‌زمینه در حالت روشن:</div>
          <div className={styles.options}>
            <Option title="سفید" type="backgroundColor" value={1} />
            <Option title="آبی" type="backgroundColor" value={2} />
            {/* <Option title="آبی 2" type="backgroundColor" value={3} /> */}
            <Option title="سرمه‌ای" type="backgroundColor" value={4} />
            <Option title="بنفش" type="backgroundColor" value={5} />
            <Option title="گرادینت بنفش" type="backgroundColor" value={6} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>رنگ سازمانی:</div>
          <div className={styles.options}>
            <Option title="سبز سازمانی" type="primaryColor" value={1} />
            <Option title="بنفش سازمانی" type="primaryColor" value={2} />
            <Option title="آبی سازمانی" type="primaryColor" value={3} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>سایز لوگو:</div>
          <div className={styles.options}>
            <Option title="کوچک" type="appLogoSize" value={1} />
            <Option title="متوسط" type="appLogoSize" value={2} />
            <Option title="بزرگ" type="appLogoSize" value={3} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>سایز عنوان شرکت:</div>
          <div className={styles.options}>
            <Option title="کوچک" type="appNameSize" value={1} />
            <Option title="متوسط" type="appNameSize" value={2} />
            <Option title="بزرگ" type="appNameSize" value={3} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>رنگ عنوان شرکت:</div>
          <div className={styles.options}>
            <Option title="معمولی" type="appNameColor" value={1} />
            <Option title="رنگ سازمانی" type="appNameColor" value={2} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>محل قرارگیری منو:</div>
          <div className={styles.options}>
            <Option title="راست" type="menuPosition" value={1} />
            <Option title="وسط" type="menuPosition" value={2} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>گروه‌بندی دکمه‌های هدر:</div>
          <div className={styles.options}>
            <Option title="مجزا" type="headerButtonGroup" value={1} />
            <Option title="ادغام‌شده" type="headerButtonGroup" value={2} />
            {settings.data.headerButtonGroup === 2 ? (
              <input
                type="text"
                value={settings.data.headerButtonText}
                onChange={(event) => {
                  dispatch({
                    type: 'settings/data',
                    payload: {
                      key: 'headerButtonText',
                      value: event.target.value,
                    },
                  });
                }}
              />
            ) : null}
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>سایز فونت منوها:</div>
          <div className={styles.options}>
            <Option title="کوچک" type="menuItemFontSize" value={1} />
            <Option title="متوسط" type="menuItemFontSize" value={2} />
            <Option title="بزرگ" type="menuItemFontSize" value={3} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>انیمیشن باز و بسته شدن منو:</div>
          <div className={styles.options}>
            <Option title="نوع اول" type="menuGroupAnimation" value={1} />
            <Option title="نوع دوم" type="menuGroupAnimation" value={2} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>موقعیت باز شدن منوی بازشونده:</div>
          <div className={styles.options}>
            <Option
              title="موازی با منوی انتخابی"
              type="menuGroupPosition"
              value={1}
            />
            <Option title="وسط" type="menuGroupPosition" value={2} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>محتوای منوی بازشونده:</div>
          <div className={styles.options}>
            <Option title="وسط‌چین" type="menuGroupType" value={1} />
            <Option title="راست‌چین" type="menuGroupType" value={2} />
            <Option
              title="راست‌چین به همراه توضیحات"
              type="menuGroupType"
              value={3}
            />
            <Option
              title="راست‌چین به همراه توضیحات و عکس"
              type="menuGroupType"
              value={4}
            />
            <Option
              title="راست‌چین و فقط با عکس"
              type="menuGroupType"
              value={5}
            />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>سایز فونت دکمه‌ها:</div>
          <div className={styles.options}>
            <Option title="کوچک" type="buttonFontSize" value={1} />
            <Option title="متوسط" type="buttonFontSize" value={2} />
            <Option title="بزرگ" type="buttonFontSize" value={3} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>انحنای دکمه‌ها:</div>
          <div className={styles.options}>
            <Option title="کم" type="buttonRadius" value={1} />
            <Option title="متوسط" type="buttonRadius" value={2} />
            <Option title="زیاد" type="buttonRadius" value={3} />
            <Option title="خیلی زیاد" type="buttonRadius" value={4} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>سایز دکمه‌ها:</div>
          <div className={styles.options}>
            <Option title="کم" type="buttonPadding" value={1} />
            <Option title="متوسط" type="buttonPadding" value={2} />
            <Option title="زیاد" type="buttonPadding" value={3} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>انیمیشن دکمه‌ها:</div>
          <div className={styles.options}>
            <Option title="داشته باشد" type="buttonAnimation" value={1} />
            <Option title="نداشته باشد" type="buttonAnimation" value={2} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>نویز پشت‌زمینه:</div>
          <div className={styles.options}>
            <Option title="نداشته باشد" type="backgroundNoise" value={1} />
            <Option title="کم" type="backgroundNoise" value={2} />
            <Option title="زیاد" type="backgroundNoise" value={3} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>جابجایی عناوین اصلی:</div>
          <div className={styles.options}>
            <Option title="حالت اول" type="headerSubHeaderSwap" value={1} />
            <Option title="حالت دوم" type="headerSubHeaderSwap" value={2} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>سایز فونت محتوای اصلی:</div>
          <div className={styles.options}>
            <Option title="کوچک" type="firstViewFontSize" value={1} />
            <Option title="متوسط" type="firstViewFontSize" value={2} />
            <Option title="بزرگ" type="firstViewFontSize" value={3} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>تصویر بالای سایت (موبایل):</div>
          <div className={styles.options}>
            <Option title="نمایش" type="mobileFirstViewImage" value={1} />
            <Option title="عدم نمایش" type="mobileFirstViewImage" value={2} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>دکمه‌ی شروع:</div>
          <div className={styles.options}>
            <Option title="نوع اول" type="startButton" value={1} />
            <Option title="نوع دوم" type="startButton" value={2} />
            <Option title="عدم نمایش" type="startButton" value={3} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>لینک اسکرول به خرید و فروش آسان:</div>
          <div className={styles.options}>
            <Option title="حالت اول" type="showOtcScrollButton" value={1} />
            <Option title="حالت دوم" type="showOtcScrollButton" value={2} />
            <Option title="عدم نمایش" type="showOtcScrollButton" value={3} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>تصویر بخش امنیت:</div>
          <div className={styles.options}>
            <Option title="‌تصویر اول" type="securityAnimation" value={1} />
            <Option title="‌تصویر دوم" type="securityAnimation" value={2} />
            <Option title="‌تصویر سوم" type="securityAnimation" value={3} />
            <Option title="‌تصویر چهارم" type="securityAnimation" value={4} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>تصویر بخش کارمزد:</div>
          <div className={styles.options}>
            <Option title="‌تصویر اول" type="feeAnimation" value={1} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>تصویر بخش استراتژی:</div>
          <div className={styles.options}>
            <Option
              title="‌تصویر اول"
              type="globalizationAnimation"
              value={1}
            />
            <Option
              title="‌تصویر دوم"
              type="globalizationAnimation"
              value={2}
            />
            <Option
              title="‌تصویر سوم"
              type="globalizationAnimation"
              value={3}
            />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>آیکن منو (موبایل):</div>
          <div className={styles.options}>
            <Option title="نوع اول" type="mobileMenuIcon" value={1} />
            <Option title="نوع دوم" type="mobileMenuIcon" value={2} />
            <Option title="نوع سوم" type="mobileMenuIcon" value={3} />
            <Option title="نوع چهارم" type="mobileMenuIcon" value={4} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>قالب منو (موبایل):</div>
          <div className={styles.options}>
            <Option title="‌نیم‌صفحه" type="mobileSidebar" value={1} />
            <Option title="تمام‌صفحه" type="mobileSidebar" value={2} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>جداسازی منوها با خط (موبایل):</div>
          <div className={styles.options}>
            <Option title="بدون خط" type="mobileMenuItemBordered" value={1} />
            <Option title="خط دار" type="mobileMenuItemBordered" value={2} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>اندازه منوها (موبایل):</div>
          <div className={styles.options}>
            <Option title="کم" type="mobileMenuItemSpacing" value={1} />
            <Option title="متوسط" type="mobileMenuItemSpacing" value={2} />
            <Option title="زیاد" type="mobileMenuItemSpacing" value={3} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>سوئیچ حالت تیره:</div>
          <div className={styles.options}>
            <Option title="حالت اول" type="darkModeSwitch" value={1} />
            <Option title="حالت دوم" type="darkModeSwitch" value={2} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>رنگ تغییرات قیمت کوین‌ها:</div>
          <div className={styles.options}>
            <Option title="حالت اول" type="coinPriceChangesColor" value={1} />
            <Option title="حالت دوم" type="coinPriceChangesColor" value={2} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>حالت نمایش واحد پول:</div>
          <div className={styles.options}>
            <Option title="عادی" type="currencyFontWeight" value={1} />
            <Option title="برجسته" type="currencyFontWeight" value={2} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>حالت نمایش هدر:</div>
          <div className={styles.options}>
            <Option title="عادی" type="headerFixed" value={1} />
            <Option title="ثابت" type="headerFixed" value={2} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>ترتیب نمایش دکمه‌هادر هدر:</div>
          <div className={styles.options}>
            <Option title="ترتیب اول" type="headerButtonOrder" value={1} />
            <Option title="ترتیب دوم" type="headerButtonOrder" value={2} />
          </div>
        </div>
      </div>
      <div className={styles.footer}>
        <button onClick={handleDownload}>دانلود خروجی</button>
      </div>
      <div className={styles.footer}>
        <button
          onClick={() => {
            dispatch({
              type: 'settings/allData',
              payload: {
                appLogoSize: 2,
                appNameColor: 2,
                appNameSize: 2,
                menuPosition: 1,
                menuItemFontSize: 1,
                buttonFontSize: 1,
                buttonRadius: 1,
                buttonPadding: 1,
                buttonAnimation: 1,
                backgroundNoise: 2,
                menuGroupAnimation: 2,
                menuGroupType: 4,
                menuGroupPosition: 2,
                headerSubHeaderSwap: 1,
                firstViewFontSize: 1,
                startButton: 2,
                showOtcScrollButton: 1,
                mobileMenuIcon: 2,
                mobileSidebar: 1,
                securityAnimation: 4,
                feeAnimation: 1,
                globalizationAnimation: 1,
                mobileMenuItemBordered: 1,
                mobileMenuItemSpacing: 2,
                primaryColor: 1,
                backgroundColor: 1,
                darkModeSwitch: 2,
                coinPriceChangesColor: 1,
                currencyFontWeight: 2,
                headerButtonGroup: 1,
              },
            });
          }}
        >
          حالت پیشفرض اول
        </button>
        <button
          onClick={() => {
            dispatch({
              type: 'settings/allData',
              payload: {
                appLogoSize: 3,
                appNameColor: 2,
                appNameSize: 2,
                menuPosition: 1,
                menuItemFontSize: 3,
                buttonFontSize: 3,
                buttonRadius: 3,
                buttonPadding: 2,
                buttonAnimation: 1,
                backgroundNoise: 2,
                menuGroupAnimation: 2,
                menuGroupType: 4,
                menuGroupPosition: 2,
                headerSubHeaderSwap: 1,
                firstViewFontSize: 3,
                startButton: 2,
                showOtcScrollButton: 1,
                mobileMenuIcon: 1,
                mobileSidebar: 1,
                securityAnimation: 4,
                feeAnimation: 1,
                globalizationAnimation: 1,
                mobileMenuItemBordered: 1,
                mobileMenuItemSpacing: 2,
                primaryColor: 1,
                backgroundColor: 2,
                darkModeSwitch: 1,
                coinPriceChangesColor: 2,
                currencyFontWeight: 1,
              },
            });
          }}
        >
          حالت پیشفرض دوم
        </button>
      </div>
    </div>
  );
}

export default Settings;
