import { useEffect, useRef, useState } from 'react';
import ArrowDownSVG from 'assets/images/arrow-down.svg';
import styles from './Picker.module.scss';

function Picker({
  label,
  containerClassName,
  labelClassName,
  pickerClassName,
  placeholder = 'یک گزینه را انتخاب کنید...',
  searchable,
  options = [],
  filterBy,
  displayProcess,
  displayName = 'name',
  uniqueId = 'id',
  imageKey,
  value,
  onChange,
}) {
  const selectRef = useRef();
  const [isOpen, setIsOpen] = useState(false);
  const [search, setSearch] = useState('');

  // close picker on click outside of it
  useEffect(() => {
    function close(event) {
      if (
        selectRef.current &&
        event.target !== selectRef.current.querySelector(`.${styles.picker}`) &&
        event.target !== selectRef.current.querySelector(`.${styles.action}`) &&
        event.target !==
          selectRef.current.querySelector(`.${styles.separator}`) &&
        event.target !==
          selectRef.current.querySelector(`.${styles.action} img`) &&
        event.target !==
          selectRef.current.querySelector(`.${styles.selected}`) &&
        event.target !== selectRef.current.querySelector('ul input')
      ) {
        setIsOpen(false);
      }
    }

    window.addEventListener('click', close, false);

    return function () {
      window.removeEventListener('click', close, false);
    };
  }, []);

  function handleToggle(event) {
    console.log('clicked');
    if (event.target === selectRef.current.querySelector('ul input')) {
      return false;
    }

    setIsOpen(!isOpen);
  }
  function handleSearchChange(event) {
    setSearch(event.target.value);
  }
  function filterOptions(option) {
    const optionDisplayValue = (
      displayProcess
        ? displayProcess(option)
        : (Object.keys(option).length ? option[displayName] : option) || ''
    )
      .toString()
      .toLowerCase();

    return optionDisplayValue.includes(search);
  }
  function handleOptionClick(selected) {
    onChange(selected);
  }

  const filteredOptions = options.filter((option) => {
    return filterBy ? filterBy(option, search) : filterOptions(option);
  });
  const selectedOption = options.filter((option) => {
    function getValue(input) {
      return Object.keys(input || {}).length ? input[uniqueId] : input;
    }

    return getValue(value) === getValue(option);
  })[0];
  const selectedValue = selectedOption
    ? displayProcess
      ? displayProcess(selectedOption)
      : (Object.keys(selectedOption).length
          ? selectedOption[displayName]
          : selectedOption) || ''
    : null;

  return (
    <div
      tabIndex="0"
      ref={selectRef}
      className={`${styles.container}${
        containerClassName ? ` ${containerClassName}` : ''
      }`}
    >
      <label
        className={`${styles.label}${
          labelClassName ? ` ${labelClassName}` : ''
        }`}
      >
        {label}
      </label>
      <div
        className={`${styles.picker}${
          pickerClassName ? ` ${pickerClassName}` : ''
        }`}
        onClick={handleToggle}
      >
        <span className={styles.selected}>
          {imageKey && selectedOption ? (
            <img src={selectedOption[imageKey]} alt={`آیکن ${selectedValue}`} />
          ) : null}
          {selectedValue ? selectedValue : placeholder}
        </span>
        <div className={styles.actions}>
          <div className={styles.action}>
            <img src={ArrowDownSVG} alt="باز و بسته کردن" />
          </div>
          {/* <span className={styles.separator} /> */}
        </div>
        {isOpen ? (
          <ul className={!(options && options.length) ? styles.empty : ''}>
            {searchable && (filteredOptions.length || search) ? (
              <input
                className={styles.search}
                placeholder="جستجو..."
                value={search}
                onChange={handleSearchChange}
                autoFocus
              />
            ) : null}
            {filteredOptions.length ? (
              <>
                {filteredOptions.map((option, index) => {
                  const displayValue = displayProcess
                    ? displayProcess(option)
                    : (Object.keys(option).length
                        ? option[displayName]
                        : option) || '';

                  return (
                    <li
                      key={index}
                      onClick={() => {
                        handleOptionClick(option);
                      }}
                    >
                      {imageKey ? (
                        <img
                          src={option[imageKey]}
                          alt={`آیکن ${displayValue}`}
                        />
                      ) : null}
                      {displayValue}
                    </li>
                  );
                })}
              </>
            ) : (
              <li className={styles.disabled}>موردی یافت نشد</li>
            )}
          </ul>
        ) : null}
      </div>
    </div>
  );
}

export default Picker;
