import Ripple from 'components/ripple/Ripple';
import { useSelector } from 'react-redux';
import styles from './Button.module.scss';

function Button({
  title = '',
  color = 'transparent',
  className = '',
  onClick,
  ...rest
}) {
  const isRippleEnabled = color !== 'transparent';
  const { settings } = useSelector((state) => {
    return state;
  });

  return isRippleEnabled ? (
    <Ripple
      className={`${styles.container} ${styles[color]} ${className} ${
        styles[`buttonAnimation${settings.data.buttonAnimation}`]
      } ${styles[`primaryColor${settings.data.primaryColor}`]} ${
        styles[`buttonFontSize${settings.data.buttonFontSize}`]
      } ${styles[`buttonRadius${settings.data.buttonRadius}`]} ${
        styles[`buttonPadding${settings.data.buttonPadding}`]
      }`}
    >
      <div {...rest} onClick={onClick}>
        {title}
      </div>
    </Ripple>
  ) : (
    <div
      {...rest}
      className={`${styles.container} ${styles[color]} ${className} ${
        styles[`buttonAnimation${settings.data.buttonAnimation}`]
      }  ${styles[`primaryColor${settings.data.primaryColor}`]} ${
        styles[`buttonFontSize${settings.data.buttonFontSize}`]
      } ${styles[`buttonRadius${settings.data.buttonRadius}`]} ${
        styles[`buttonPadding${settings.data.buttonPadding}`]
      }`}
      onClick={onClick}
    >
      {title}
    </div>
  );
}

export default Button;
