import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';
import Header from 'components/header/Header';
import HomePage from 'pages/home/HomePage';
import Settings from 'components/settings/Settings';
import Menu from 'components/menu/Menu';
import Menu2 from 'components/menu/Menu2';
import Menu3 from 'components/menu/Menu3';
import styles from './App.module.scss';

function App() {
  const { settings } = useSelector((state) => {
    return state;
  });

  // set background color related class on body
  useEffect(() => {
    document.body.setAttribute(
      'data-dark-background',
      settings.data.backgroundColor === 1 ? 'false' : 'true'
    );
    document.body.setAttribute(
      'background-color',
      settings.data.backgroundColor
    );
  }, [settings.data.backgroundColor]);

  return (
    <div
      className={`${styles.container} ${
        styles[`backgroundNoise${settings.data.backgroundNoise}`]
      } ${styles[`backgroundColor${settings.data.backgroundColor}`]}`}
    >
      {settings.isVisible ? <Settings /> : null}
      <Menu />
      {/* <Menu2 /> */}
      {/* <Menu3 /> */}
      <Header />
      <HomePage />
    </div>
  );
}

export default App;
