const initialState = {
  isVisible: true,
  data: {
    appLogoSize: 2,
    appNameColor: 2,
    appNameSize: 2,
    menuPosition: 1,
    menuItemFontSize: 1,
    buttonFontSize: 1,
    buttonRadius: 1,
    buttonPadding: 1,
    buttonAnimation: 1,
    backgroundNoise: 2,
    menuGroupAnimation: 2,
    menuGroupType: 4,
    menuGroupPosition: 2,
    headerSubHeaderSwap: 1,
    firstViewFontSize: 1,
    startButton: 3,
    showOtcScrollButton: 2,
    mobileMenuIcon: 2,
    mobileSidebar: 1,
    securityAnimation: 4,
    feeAnimation: 1,
    globalizationAnimation: 1,
    mobileMenuItemBordered: 1,
    mobileMenuItemSpacing: 2,
    primaryColor: 2,
    backgroundColor: 1,
    darkModeSwitch: 2,
    coinPriceChangesColor: 1,
    currencyFontWeight: 2,
    headerButtonGroup: 2,
    headerButtonText: 'شروع کنید',
    mobileFirstViewImage: 2,
    headerFixed: 2,
    headerButtonOrder: 2,
  },
};

function SettingsReducer(state = initialState, action) {
  switch (action.type) {
    case 'settings/data':
      const newState = { ...state };
      newState.data[action.payload.key] = action.payload.value;

      return newState;

    case 'settings/allData':
      return {
        ...state,
        data: action.payload,
      };

    case 'settings/show':
      return {
        ...state,
        isVisible: true,
      };

    case 'settings/hide':
      return {
        ...state,
        isVisible: false,
      };

    default:
      return state;
  }
}

export default SettingsReducer;
