import styles from './Ripple.module.scss';

function Ripple({ className, onClick, ...rest }) {
  function createRipple(event) {
    const pressable = event.currentTarget;

    const circle = document.createElement('span');
    const diameter = Math.max(pressable.clientWidth, pressable.clientHeight);
    const radius = diameter / 2;

    circle.style.width = circle.style.height = `${diameter}px`;
    circle.style.left = `${
      event.clientX -
      (pressable.offsetLeft
        ? pressable.offsetLeft
        : pressable.offsetParent
        ? pressable.offsetParent.offsetLeft
        : 0) -
      radius
    }px`;
    circle.style.top = `${
      event.clientY -
      (pressable.offsetTop
        ? pressable.offsetTop
        : pressable.offsetParent
        ? pressable.offsetParent.offsetTop
        : 0) -
      radius
    }px`;
    circle.classList.add(styles.ripple);

    const ripple = pressable.querySelector(`.${styles.ripple}`);

    if (ripple) {
      ripple.remove();
    }

    pressable.appendChild(circle);
  }

  function handleClick(event) {
    createRipple(event);

    if (onClick) {
      onClick(event);
    }
  }

  return (
    <div
      {...rest}
      className={`${styles.container}${className ? ` ${className}` : ''}`}
      onClick={handleClick}
    />
  );
}

export default Ripple;
