import { useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Lottie from 'react-lottie';
import Button from 'components/button/Button';
import * as animationData1 from 'assets/images/lottie/86719-cryptocurrency.json';
import * as animationData2 from 'assets/images/lottie/101976-exchange.json';
import LogoSVG from 'assets/images/logo.svg';
import LogoSVG2 from 'assets/images/logo-purple.svg';
import LogoSVG3 from 'assets/images/logo-blue.svg';
import ArrowDownSVG from 'assets/images/arrow-down.svg';
import Menu1SVG from 'assets/images/menu1.svg';
import Menu2SVG from 'assets/images/menu2.svg';
import Menu3SVG from 'assets/images/menu3.svg';
import Menu4SVG from 'assets/images/menu4.svg';
import BrightnessSVG from 'assets/images/brightness.svg';
import styles from './Header.module.scss';
import Switch from 'components/switch/Switch';

const MENUS = [
  {
    title: 'روند بازار',
  },
  {
    title: 'خرید و فروش آسان',
  },
  {
    title: 'مطالب',
    image: animationData1,
    items: [
      { title: 'اخبار', subTitle: 'آخرین اخبار مربوط به حوزه‌ی ارز دیجیتال' },
      { title: 'اطلاعیه', subTitle: 'تمامی اطلاعیه‌های مجموعه‌ی ثمین‌کوین' },
      { title: 'کوین‌ها', subTitle: 'توضیحات مربوط به هر یک از کوین‌ها' },
      { title: 'تبلیغات', subTitle: 'آخرین رویدادها و تبلیغات' },
    ],
  },
  {
    title: 'ویژگی‌ها',
    image: animationData2,
    items: [
      { title: 'جوایز', subTitle: 'روش‌های موجود برای دریافت جوایز مختلف' },
      { title: 'کارت هدیه', subTitle: 'ایجاد و اهدای کارت هدیه‌ی رمز دیجیتال' },
      {
        title: 'معرفی به دوستان',
        subTitle: 'معرفی ثمین‌کوین به دوستان و دریافت امتیاز',
      },
      { title: 'NFT', subTitle: 'همه‌چیز در مورد پروژه‌ی NFT ثمین‌کوین' },
    ],
  },
  {
    title: 'درباره',
    items: [
      { title: 'ثمین‌کوین', subTitle: 'درباره مجموعه و فعالیت‌های آن' },
      { title: 'تماس با ما', subTitle: 'اطلاعات تماس و راه‌های ارتباطی' },
    ],
  },
];

function MenuGroup({ items = [], image, isOpen }) {
  const { settings } = useSelector((state) => {
    return state;
  });

  return (
    <div
      className={`${styles.group}${isOpen ? ` ${styles.visible}` : ''} ${
        styles[`menuGroupAnimation${settings.data.menuGroupAnimation}`]
      } ${styles[`menuGroupType${settings.data.menuGroupType}`]} ${
        styles[`menuGroupPosition${settings.data.menuGroupPosition}`]
      }`}
    >
      <div className={styles.context}>
        {items.map((item, index) => {
          return (
            <div key={index} className={styles.groupItem}>
              <div className={styles.title}>{item.title}</div>
              {[3, 4].indexOf(settings.data.menuGroupType) !== -1 ? (
                <div className={styles.subTitle}>{item.subTitle}</div>
              ) : null}
            </div>
          );
        })}
      </div>
      {image && [4, 5].indexOf(settings.data.menuGroupType) !== -1 ? (
        <div className={styles.imageContainer}>
          <Lottie
            options={{
              loop: true,
              autoplay: true,
              animationData: image,
              rendererSettings: {
                preserveAspectRatio: 'xMidYMid slice',
              },
            }}
          />
        </div>
      ) : null}
    </div>
  );
}
function MenuItem({ title = '', image, items = [] }) {
  const ref = useRef();
  const [isOpen, setIsOpen] = useState(false);
  const { settings } = useSelector((state) => {
    return state;
  });

  // close select on click outside of it
  useEffect(() => {
    function close(event) {
      if (
        event.target !==
          ref.current.querySelector(`.${styles.itemContainer}`) &&
        event.target !==
          ref.current.querySelector(`.${styles.itemContainer} span`) &&
        event.target !==
          ref.current.querySelector(
            `.${styles.itemContainer} .${styles.toggleIcon}`
          )
      ) {
        setIsOpen(false);
      }
    }

    window.addEventListener('click', close, false);

    return function () {
      window.removeEventListener('click', close, false);
    };
  }, []);

  function handleClick() {
    setIsOpen(!isOpen);
  }

  return (
    <div
      className={`${styles.item} ${
        styles[`menuItemFontSize${settings.data.menuItemFontSize}`]
      }`}
      ref={ref}
    >
      <div className={`${styles.itemContainer}`} onClick={handleClick}>
        <span>{title}</span>
        {items.length ? (
          <img
            className={styles.toggleIcon}
            src={ArrowDownSVG}
            alt="باز و بسته کردن منو"
          />
        ) : null}
      </div>
      {items.length ? (
        <MenuGroup isOpen={isOpen} image={image} items={items} />
      ) : null}
    </div>
  );
}
function Header() {
  const dispatch = useDispatch();
  const [menuIcon, setMenuIcon] = useState(Menu1SVG);
  const [isDarkMode, setIsDarkMode] = useState(false);
  const { settings } = useSelector((state) => {
    return state;
  });

  useEffect(() => {
    switch (settings.data.mobileMenuIcon) {
      case 1:
        setMenuIcon(Menu1SVG);
        break;
      case 2:
        setMenuIcon(Menu2SVG);
        break;
      case 3:
        setMenuIcon(Menu3SVG);
        break;

      case 4:
        setMenuIcon(Menu4SVG);
        break;

      default:
        setMenuIcon(Menu1SVG);
        break;
    }
  }, [settings.data.mobileMenuIcon]);

  function openSettings() {
    dispatch({ type: 'settings/show' });
  }
  function toggleClassMenu() {
    let menu = document.querySelector('.mobile-menu');

    menu.classList.add('menu--animatable');
    if (!menu.classList.contains('menu--visible')) {
      menu.classList.add('menu--visible');
      document.body.style.overflow = 'hidden';
    } else {
      menu.classList.remove('menu--visible');
      document.body.style.overflow = 'initial';
    }
  }
  function handleDarkModeClick() {
    if (document.documentElement.getAttribute('data-theme') === 'light') {
      document.documentElement.setAttribute('data-theme', 'dark');
      document
        .querySelector('meta[name="theme-color"]')
        .setAttribute('content', '#2f3136');
    } else {
      document.documentElement.setAttribute('data-theme', 'light');
      document
        .querySelector('meta[name="theme-color"]')
        .setAttribute('content', '#ffffff');
    }
  }

  return (
    <header
      className={`${styles.container} ${
        styles[`backgroundColor${settings.data.backgroundColor}`]
      } ${styles[`backgroundNoise${settings.data.backgroundNoise}`]} ${
        styles[`headerFixed${settings.data.headerFixed}`]
      }`}
    >
      <div className={styles.appInfo}>
        <img
          className={`${styles.menuIcon}`}
          src={menuIcon}
          alt="منو"
          onClick={toggleClassMenu}
        />
        <img
          className={`${styles.logo} ${
            styles[`appLogoSize${settings.data.appLogoSize}`]
          }`}
          src={
            settings.data.primaryColor === 1
              ? LogoSVG
              : settings.data.primaryColor === 2
              ? LogoSVG2
              : settings.data.primaryColor === 3
              ? LogoSVG3
              : LogoSVG
          }
          alt="ثمین‌کوین"
          onClick={openSettings}
        />
        <h1
          className={`${styles[`appNameColor${settings.data.appNameColor}`]} ${
            styles[`appNameSize${settings.data.appNameSize}`]
          } ${styles[`primaryColor${settings.data.primaryColor}`]}`}
        >
          ثمین‌کوین
        </h1>
      </div>

      <div
        className={`${styles.menu} ${
          styles[`menuPosition${settings.data.menuPosition}`]
        }`}
      >
        {MENUS.map((menu, index) => {
          return (
            <MenuItem
              key={index}
              title={menu.title}
              image={menu.image}
              items={menu.items}
            />
          );
        })}
      </div>

      <div
        className={`${styles.buttons} ${
          styles[`headerButtonGroup${settings.data.headerButtonGroup}`]
        } ${styles[`headerButtonOrder${settings.data.headerButtonOrder}`]}`}
      >
        {settings.data.darkModeSwitch === 1 ? (
          <Switch
            checked={isDarkMode}
            onChange={(event) => {
              setIsDarkMode(event.target.checked);
              handleDarkModeClick();
            }}
          />
        ) : null}
        {settings.data.darkModeSwitch === 2 ? (
          <img
            src={BrightnessSVG}
            alt="حالت تیره و روشن"
            onClick={handleDarkModeClick}
          />
        ) : null}
        {settings.data.headerButtonGroup === 1 ? (
          <>
            <Button className={styles.button} title="ورود" />
            <Button className={styles.button} title="ثبت نام" color="primary" />
          </>
        ) : (
          <Button
            className={styles.button}
            title={settings.data.headerButtonText || 'ورود و ثبت نام'}
            color="primary"
          />
        )}
      </div>
    </header>
  );
}

export default Header;
